import '../App.css'
import "../style.css"

import { useState, useEffect } from 'react'
import {BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';


export default function Home(props) {
  const [data, setData] = useState() 
  const [index, setIndex] = useState (0)

  const getDataFromJson = () => {
    var fileName = "https://romanbr87.github.io/links/db1.json";
    fetch(fileName)
    .then(response => response.json())
    .then(data => {
      var jobs_data = data.job;
      setData (jobs_data);
      
      //var len = jobs_data.reduce((acc,element) => acc + element.links.length, 0)
      //let intro = document.getElement ('intro');   
      //intro.innerHTML = jobs_data.length + " categories " + len + " websites"
      //console.log (len);
    })
    .catch((error) => {
      console.error('Error:', error);
    });
  }
  
  const AdjustNum = (num) => (num < 10) ? "0" + num : num
  
  const soryByAtrr = (arr, attr) => {
    if (attr == undefined) {
      return arr;
    }

    else {
      arr = arr.sort((a, b) => {
          let res = a["site_name"].localeCompare(b["site_name"])
          return res;
      })
      //console.log (arr);
      return arr;
    }

  }

  const setTable = (e) => {
    e.preventDefault();
    setIndex(e.target.getAttribute('a-key'));
  }

  useEffect(() => {
    getDataFromJson ();
  })

  if (data == null || data == "") return <p>Null</p>

  return (
    <div className="container text-center" dir='rtl'>

      <div className="list-group text-right">
      <Link to="javascript:void(0);" className="list-group-item active">
        <span className="badge text-left" style={{ float: 'left'}}>{ data.reduce((acc,element) => acc + element.links.length, 0) }</span>
        קטגוריות אתרים
      </Link>

      {
        data.map ((e, i) => {
          return (<Link className="list-group-item" to="javascript:void(0);" key={i} a-key={i} onClick={ event => setTable(event) }>
            <span className="badge" style={{ float: 'left'}} a-key={i} onClick={ event => setTable(event) }>{ e.links.length }</span>
            {AdjustNum (i+1) + ". " + e.name}
          </Link>) 
        })
      }
      </div>

      <select className="form-control" id="sel" onChange={ e => setIndex(e.target.value) } >
        {
          data.map ((e, i) => {
            return <option key={i+1} value={i}>{AdjustNum(i+1) + ". " + e.name + " - " + e.links.length + " קישורים" }</option> 
          })
        }
      </select>


      <div className="panel panel-info" style={{ marginTop: "1%" }}>
        <div className="panel-heading"><h3 style = {{ fontWeight: 'bolder'}} style={{ margin: '0', padding: '0.25em 0 0.25em 0'}}>{ data[index].name } </h3></div>
        <table className="panel-body table table-bordered table-striped table-hover table-condensed text-center" id="testTable">
          <thead>
            <tr style={{backgroundColor: "yellow" }}>
              <th className="numberCell">#</th>
              <th className="nameCell">שם</th>
              <th>קישור 2</th>
              <th>דף פייסבוק</th>
              <th>2דף פייסבוק</th>
              <th>לינקדאין</th>
              <th>אינסטגרם</th>
            </tr>
          </thead>
          <tbody id="dataTable">
          {
              soryByAtrr(data[index].links, data[index].sort).map ((e, i) => {
              return ([
                (e.border != undefined) ? 
                <tr className="borderTop"><td colSpan="100"><h4>{ e.border }</h4></td></tr> : "",
                
                <tr key={i} className={ (e.border != undefined) ? "borderTop": ''}> 
                  <td className="numberCell">{ AdjustNum(i+1)}</td>
                  <td className="nameCell">
                  { (e.link != '') ? <Link to={e.link} >{e.site_name}</Link> : e.site_name }
                  </td>
                  <td>
                  { (e.link2 != '') ? <Link to={e.link2}>קישור 2</Link> : 'קישור 2' }
                  </td>
                  <td>
                  { (e.facebook_link1 != '') ? <Link to={e.facebook_link1}>דף פייסבוק</Link> : 'דף פייסבוק' }
                  </td>
                  <td>
                  { (e.facebook_link2 != '' && e.facebook_link2 != undefined) ? <Link to={e.facebook_link2}>דף פייסבוק 2</Link> : 'דף פייסבוק 2' }
                  </td>
                  <td>
                  { (e.linkedIn_link != '') ? <Link to={e.linkedIn_link}>לינקדאין</Link> : 'לינקדאין' }
                  </td>
                  <td>
                  { (e.instagram_link != '' && e.instagram_link != undefined) ? <Link to={e.instagram_link}>אינסטגרם</Link> : 'אינסטגרם' }
                  </td>
                </tr>
              ])
            }) 
          }
          </tbody>
        </table>
      </div>

    </div>
  )
}
  